import {
  Flex,
  Box,
  FormControl,
  FormLabel,
  Input,
  InputGroup,
  HStack,
  InputRightElement,
  Stack,
  Button,
  Heading,
  Text,
  Link,
  Icon,
} from "@chakra-ui/react";
import { Navigate, useNavigate, Link as reachLink } from "react-router-dom";
import { useState } from "react";
import { AiFillEyeInvisible, AiFillEye } from "react-icons/ai";
import { createUserWithEmailAndPassword, updateProfile } from "firebase/auth";
import { auth } from "../FirebaseConfig";
import axios from "axios";
const register=()=>{
  axios.post(
    "/register",
    {
      uid:auth.currentUser.uid,
      name:auth.currentUser.displayName

    },
    {
      headers: {
        Authorization: auth.currentUser.accessToken,
        'Content-Type': 'application/json'
      },
    }
  );
}
export default function SignupCard() {
  const [showPassword, setShowPassword] = useState(false);
  const [email, setEmail] = useState(null);
  const [password, setPassword] = useState(null);
  const [fname, setFname] = useState(null);
  const [lname, setLname] = useState(null);
  const [error, Seterror] = useState({
    code: null,
    error: false,
    message: null,
  });
  const navigate = useNavigate();
  const signup = (event) => {
    event.preventDefault();
    console.log(email);
    if (
      email !== "" &&
      email !== null &&
      password !== "" &&
      password !== null &&
      fname !== null &&
      fname !== ""
    ) {
      console.log(fname);
      createUserWithEmailAndPassword(auth, email, password)
        .then((userCredential) => {
          
          if (lname === null || lname === "") {
            updateProfile(auth.currentUser, {
              displayName: `${fname}`,
            }).then(() => {
              
              register()
            });
            
          } else {
            updateProfile(auth.currentUser, {
              displayName: `${fname} ${lname}`,
            }).then(() => {
              
              register()
            });
          }
        
          
          navigate("/");
         
        })
        .catch((error) => {
          if (error.code === "auth/email-already-in-use") {
            Seterror({
              code: error.code,
              error: true,
              message: `${email} is already in use`,
            });
          } else if (error.code === "auth/invalid-email") {
            Seterror({
              code: error.code,
              error: true,
              message: "use valid email",
            });
          } else {
            Seterror({
              code: "some thing went wrong",
              error: true,
              message: "Something went worng ",
            });
          }
          console.log(error.code);
        });
    } else {
      Seterror({
        code: "email empty",
        error: true,
        message: "fill required filds marked with *",
      });
      console.log(email);
    }
  };
  const handleFname = (event) => {
    setFname(event.target.value);
    Seterror({
      code: null,
      error: false,
      message: null,
    });
  };
  const handleLname = (event) => {
    setLname(event.target.value);
    Seterror({
      code: null,
      error: false,
      message: null,
    });
  };
  const handleEmail = (event) => {
    setEmail(event.target.value);
    Seterror({
      code: null,
      error: false,
      message: null,
    });
  };
  const handlePassword = (event) => {
    setPassword(event.target.value);
    Seterror({
      code: null,
      error: false,
      message: null,
    });
  };
  if (!auth.currentUser) {
    return (
      <Flex
        mt={91}
        minH={"100vh"}
        align={"center"}
        justify={"center"}
        // bg={useColorModeValue("gray.50", "gray.800")}
      >
        <Stack spacing={8} mx={"auto"} maxW={"lg"} py={12} px={6}>
          <Stack align={"center"}>
            <Heading fontSize={"4xl"} textAlign={"center"}>
              Sign up
            </Heading>
            <Text fontSize={"lg"} color={"gray.600"}>
              to enjoy all of our cool features 
            </Text>
          </Stack>
          <Box
            rounded={"lg"}
            // bg={useColorModeValue("white", "gray.700")}
            boxShadow={"lg"}
            p={8}
          >
            <Stack spacing={4}>
              <Text
                h={10}
                d={"flex"}
                opacity={error.error ? 1 : 0}
                p="2"
                alignItems="center"
                justifyContent={"center"}
                borderRadius={10}
                backgroundColor="rgba(232, 39, 39, 0.5)"
                align={"center"}
                color="white"
                transition="opacity 0.5s ease"
              >
                {error.message}
                {"    "}
              </Text>
              <form onSubmit={signup}>
                <HStack>
                  <Box>
                    <FormControl id="firstName" isRequired>
                      <FormLabel>First Name</FormLabel>
                      <Input onChange={handleFname} type="text" />
                    </FormControl>
                  </Box>
                  <Box>
                    <FormControl id="lastName">
                      <FormLabel>Last Name</FormLabel>
                      <Input onChange={handleLname} type="text" />
                    </FormControl>
                  </Box>
                </HStack>
                <FormControl id="email" isRequired>
                  <FormLabel>Email address</FormLabel>
                  <Input
                    autoComplete="username"
                    onChange={handleEmail}
                    type="email"
                  />
                </FormControl>
                <FormControl id="password" isRequired>
                  <FormLabel>Password</FormLabel>
                  <InputGroup>
                    <Input
                      onChange={handlePassword}
                      autoComplete="current-password"
                      type={showPassword ? "text" : "password"}
                    />
                    <InputRightElement h={"full"}>
                      <Button
                        variant={"ghost"}
                        onClick={() =>
                          setShowPassword((showPassword) => !showPassword)
                        }
                      >
                        <Icon
                          as={showPassword ? AiFillEye : AiFillEyeInvisible}
                        />
                      </Button>
                    </InputRightElement>
                  </InputGroup>
                </FormControl>
                <Stack spacing={10} pt={2}>
                  <Button
                    loadingText="Submitting"
                    type="submit"
                    size="lg"
                    bg={"blue.400"}
                    color={"white"}
                    _hover={{
                      bg: "blue.500",
                    }}
                  >
                    Sign up
                  </Button>
                </Stack>
              </form>
              <Stack pt={3}>
                <Text pt={3} align={"center"}>
                  Already a user?{" "}
                  <Link as={reachLink} to="/auth/login" color={"blue.400"}>
                    Login
                  </Link>
                </Text>
              </Stack>
            </Stack>
          </Box>
        </Stack>
      </Flex>
    );
  } else {
    return <Navigate to="/web" />;
  }
}
